.grid {

  @include onTablet {
    @include useGrid(6, 24px);

  }

  @include onDesktop {
    @include useGrid(12, 24px);

  }

  &__item {
    align-items: center;

    &--desktop-6-11 {
      font-style: normal;
      font-weight: 500;
      font-size: 23px;
      line-height: 140%;
      color: $whiteColor;
      padding-bottom: 32px;

      @include onDesktop {
        margin-bottom: 12px;
        // max-width: 498px; ///
      }
    }

    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        @include onDesktop {
          &--desktop-#{$start}-#{$end} {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }

    @for $start from 1 through 6 {
      @for $end from $start through 6 {
        @include onTablet {
          &--tablet-#{$start}-#{$end} {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }
}
